import React,{ Component }  from 'react';
import '../../App.css';
import { Card ,Button,Input, Divider} from 'antd';
import LGC from '../../ABI/LGC';

class Hangqinadd extends Component {
      state = {
        userAdr:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  }

  transfer = async () =>{
    await LGC.methods.transfer(
        this.state.userAdr, '10')
    .send({from:window.defaultAccount});
  }


  render(){

  return (
    <div>
        <Card type="inner" size='small' >
            <Divider >什么是信用分？</Divider>
            <div>
               <p><b>什么是信用分？</b>
                 <div>&nbsp;&nbsp;&nbsp;&nbsp;由于区块链的匿名性，也使得人性的恶充分的显现。为了杜绝撸客，链改网开世界先河创造性的发明了信用分制度。
                      信用分满分10分，各种生态适用不同的信用分。并不是每种生态都需要考查您的信用分。
                  </div>
               </p>
               <p><b>如何获得信用分？</b>
                 <div>&nbsp;&nbsp;&nbsp;&nbsp;在您第一次访问链改网时，会提示您申请信用分，我们通常会在24小时内审核并向您发送信用10分。
                   或者通过您的朋友(有授信权限者)获得。
                  </div>
               </p>
               <p><b>申请信用分不被审核通过的几种原因？</b>
                  <div>1.已有一个号，其它号不被通过</div>
                  <div>2.存在过恶意行为</div>
                  <div>审核可能存在纰漏，如果您的帐号没有以上情况，24小时依然没有获得信用分，可再次申请，我们会重新审核。</div>
               </p>
               <p><b>会被扣分吗？</b>
                 <div>以下行为会被扣分：
                      <div>1.恶意刷单,扣9分</div>
                  </div>
               </p>
            </div>
            <Divider >授予信用分(仅授权者可用)</Divider>
                <div style={{marginTop:'5px'}}>伙伴地址：
                </div>
                <Input  value={this.state.userAdr} placeholder="伙伴的火币链地址" 
                      onChange={event=>{this.setState({userAdr:event.target.value})}}/>
                <Button  shape='round' block type="primary"  size="middle" style={{marginTop:'6px'}} 
                      onClick={() => {this.transfer()}}>发送</Button>
         </Card>
    </div>
  );
}
}

export default Hangqinadd;
