import React from 'react';
import {  Card,Divider } from 'antd';

function Genxin() {
  return(
    <div>
        <Card>
            <div style={{color:"black",fontSize:"13px"}}>           
                <h3 style={{color:"blue"}}>通知2021-4-15:</h3>
                <p>
                    <p>上线信用制度</p>
                </p>
            </div>

            <Divider/>
            <div style={{color:"black",fontSize:"13px"}}>           
                <h3 style={{color:"blue"}}>通知2021-4-12:</h3>
                <p>
                    <p>为了永绝撸客，链改网将上线信用制度。</p>
                    <p>每日一领中的额度
                        领完后将在信用制度上线后按原来的制度(领取秒到)再次开放，
                        请尽量在满10枚到帐后不再领取。</p>
                    <p>理论上每个人都可以满10枚，
                        如果您手气实在太差未足额10枚而未到帐，只能表示遗憾^_^!</p>
                    <p>敬请理解与支持。</p>
                </p>
            </div>
            <Divider/>
            <div style={{color:"black",fontSize:"13px"}}>           
                <h3 style={{color:"blue"}}>更新2021-3-30:</h3>
                <p>
                    上线行情，任何人可以把喜欢的通证加入行情页
                </p>
            </div>
            <Divider/>
            <div style={{color:"black",fontSize:"13px"}}>

                
                <h3 style={{color:"blue"}}>更新2021-3-27:</h3>
                <p>
                    上线红包生态，开启发红包挖矿。
                </p>
            </div>
            
            <div style={{color:"black",fontSize:"13px"}}>
            <Divider/>
                <h3 style={{color:"blue"}}>更新2021-3-12:</h3>
                <p>
                    将每日一领的额度改为随机额度，您将会随机得到LG,每天试试手气享受乐趣，拿到最佳手气记得向朋友分享喜悦哦。
                </p>
                
                <p>
                    也是希望能够留住大家对本站的关注。本站不圈钱、不募资，只想踏踏实实深耕区块链，由浅至深，将区块链赋能实体经济是我们的方向。
                </p>
                <p>
                    新的生态上线以及界面美化等等需要不少时间，还希望大家保持耐心，支持真正的区块链创业者。
                </p>
            </div>
        </Card>
    </div>
  )

}

export default Genxin;
