import React,{ Component }  from 'react';
import '../../App.css';
import { Card, Input,Button, Popover } from 'antd';
import { QuestionCircleOutlined, QuestionOutlined} from '@ant-design/icons';
import web3 from "../../ABI/web3";
import LGC from "../../ABI/LGCget";
import GXzhi from '../../ABI/GxzhiABI';
import erc20abi from '../../ABI/ERC20.abi.json';
import HangQin from '../../ABI/hangqin';
import { Link} from 'react-router-dom';



class maincard1 extends Component {
  state = {
    LGCadr:'0x91de2f1946ba78302a531eeadac95e103f6ac813',  //LGC信用分地址
    key: '账户',
    noTitleKey: '账户',
    user:"",
    LGbalance:"",
    DKJbalance:"",
    YHTbalance:"",
    Airdrop_LGbalance:"",
    balance:"",
    GXvalue:"",
    chainId:"",
    GxianID:"",
    chainWarning:"",
    message:"",
    gxSwaped:'',
    Usertotal:'',
    LGCbalance:'',
    LGCclose:'',
    disabled:'',
    
    RMB_LG:'',
    RMB_YHT:'',
    RMB_DKJ:'',

  };

  async componentDidMount(){
    //判断页面是否安装了Metamask
    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        console.log(accounts)
  
        //初始化Provider
        const provider = window['ethereum']
  
        //获取网络ID
        if(provider.chainId == 0x80){this.setState({chainId:"火币链"})};
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({chainWarning:"请使用火币链，以太链目前仅可以用“入口”"})};
        
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}
  
    //设置变量中默认用户的帐号
    const user = window.defaultAccount;
    this.setState({user});
    
    //获取用户帐号中的LGC余额
    const LGC = new web3.eth.Contract(erc20abi.abi,this.state.LGCadr);
    const LGCbalance = await LGC.methods.balanceOf(window.defaultAccount).call();
    this.setState({LGCbalance:LGCbalance});
    if(LGCbalance === '0'){
      this.setState({LGCclose:'block'})
    }else{
      this.setState({LGCclose:'none'})
    }
    console.log(LGCbalance,this.state.LGCclose)

    //获取用户帐号中的LG余额
    const LG = new web3.eth.Contract(erc20abi.abi,'0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c');
    const LGbalance = await LG.methods.balanceOf(window.defaultAccount).call();
    this.setState({LGbalance:parseFloat(web3.utils.fromWei(LGbalance,"ether")).toFixed(2)});
    const LGtokenPrice = await HangQin.methods.getTOKENprice(11).call();
      LGtokenPrice[2] = parseFloat(web3.utils.fromWei(LGtokenPrice[2],"ether")).toFixed(3);
      const RMB_LG = (this.state.LGbalance * LGtokenPrice[2]).toFixed(2);
      this.setState({RMB_LG:RMB_LG});

    //获取用户帐号中的YHT余额
    const YHT = new web3.eth.Contract(erc20abi.abi,'0x158c9af12137cF8dfd86b3bD191DbAB79D41C856');
    const YHTbalance = await YHT.methods.balanceOf(window.defaultAccount).call();
    this.setState({YHTbalance:parseFloat(web3.utils.fromWei(YHTbalance,"ether")).toFixed(2)});
    const YHTtokenPrice = await HangQin.methods.getTOKENprice(12).call();
      YHTtokenPrice[2] = parseFloat(web3.utils.fromWei(YHTtokenPrice[2],"ether")).toFixed(3);
      const RMB_YHT = (this.state.YHTbalance * YHTtokenPrice[2]).toFixed(2);
      this.setState({RMB_YHT:RMB_YHT});

    //获取用户帐号中的DKJ余额
    const DKJ = new web3.eth.Contract(erc20abi.abi,'0x98812b562a8684910575c22c40364ecb7bdb5bfb');
    const DKJbalance = await DKJ.methods.balanceOf(window.defaultAccount).call();
    this.setState({DKJbalance:parseFloat(web3.utils.fromWei(DKJbalance,"ether")).toFixed(2)});
    const DKJtokenPrice = await HangQin.methods.getTOKENprice(13).call();
      DKJtokenPrice[2] = parseFloat(web3.utils.fromWei(DKJtokenPrice[2],"ether")).toFixed(3);
      const RMB_DKJ = (this.state.DKJbalance * DKJtokenPrice[2]).toFixed(2);
      this.setState({RMB_DKJ:RMB_DKJ});

  
    //获取用户帐号中的HT余额
    const balance = await web3.eth.getBalance(window.defaultAccount);
    const b = (balance/10**18).toFixed(4)
    this.setState({balance:b});

    //获取贡献值
    const GXvalue = await GXzhi.methods.getGongxianzhi(window.defaultAccount).call();
    this.setState({GXvalue:parseFloat(web3.utils.fromWei(GXvalue[2],"ether")).toFixed(2)});
    this.setState({gxSwaped:parseFloat(web3.utils.fromWei(GXvalue[3],"ether")).toFixed(2)});
    if(this.state.GXvalue - this.state.gxSwaped == 0){
      this.setState({disabled:'disabled'})
    }

    //获取贡献人数
    const GxianID = await GXzhi.methods.userID().call();
    this.setState({GxianID});
    }

    swapGX =async () => {
      await GXzhi.methods.swapGX().send({from:window.defaultAccount});
    }

    onTabChange = (key, type) => {
      this.setState({ [type]: key });
    };

    getLGC =async () => {
      this.setState({message:"等待打包……"});
      await LGC.methods.getLGC().send({from:window.defaultAccount});
      this.setState({message:"发送成功，请等待后台审批,通常24小时内通过。已为您发送小额LG，您可以先在钱包首页添加好资产(搜LG即可添加)"});
    }

  render(){

    const content1 = (
      <div>
        通过使用生态获得，比如发红包等，将来还会增加更多的生态
      </div>
    );

    const content2 = (
      <div>
        您已挖回家的LG
      </div>
    );

    const content3 = (
      <div>
        <p>为什么是灰色？预挖矿-已挖矿=0；</p>
        <p>快去发个红包吧。</p>
        <p>可以预挖多一些之后再一次性挖出。</p>
      </div>
    );

    const content4 = (
      <div>
        <p>每日一领挖出的LG,每满一定额度会自动进入您的帐户</p>
      </div>
    );

    const tabListNoTitle = [
      {key: '账户',tab: '账户',},
      // {key: '资产',tab: '资产',},
      {key: '挖矿',tab: '挖矿',},
    ];
    
    const contentListNoTitle = {
      账户: 
        <div>
            <p>当前链：{this.state.chainId}
                <div style={{background:"yellow", color:"red",fontSize:"14px"}}>
                  {this.state.chainWarning}
                </div>
            </p>
            <p >
            <Input id='i1' addonBefore="帐号" value={this.state.user} disabled="true"/>
            </p>
            <Link to='/LGCtransfer'>
                <div to='/LGCtransfer' className="gongxian"><QuestionCircleOutlined />信用分(LGC)<span style={{float:"right"}}>{this.state.LGCbalance}</span></div>
            </Link>
                <div style={{display:this.state.LGCclose, color:'red', marginTop:'10px'}} >
                    <div>链改网欢迎您，请先领取信用分。</div>
                    <div>注：一人一号，小号领取将拒绝。</div>
                    <Button  onClick={() => {this.getLGC()}} 
                          type="danger"  
                          size='larger'
                          shape='round'
                          block >
                          领取信用分
                      </Button>
                </div>
                <div style={{color:"green",fontSize:"10px"}}>
                  {this.state.message}
                </div>
            
        </div>,
      资产: 
          <div >
              <p className="gongxian">HT<span style={{float:"right"}}>{this.state.balance}</span></p>
              <p className="gongxian">LG<span style={{float:"right"}}>{this.state.LGbalance}(￥{this.state.RMB_LG})</span></p>
              <p className="gongxian">YHT<span style={{float:"right"}}>{this.state.YHTbalance}(￥{this.state.RMB_YHT})</span></p>
              <p className="gongxian">DKJ<span style={{float:"right"}}>{this.state.DKJbalance}(￥{this.state.RMB_DKJ})</span></p>
          </div>,
      挖矿: 
          <div>
              <p className="gongxian">
                  <Popover content={content1} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>
                  &nbsp;&nbsp;生态预挖矿
                  <span style={{float:"right"}}>{this.state.GXvalue}</span>
              </p>
              <p className="gongxian">
                    <Popover content={content2} trigger="click">
                        <QuestionCircleOutlined /> 
                    </Popover>
                    &nbsp;&nbsp;生态已挖矿<span style={{float:"right"}}>{this.state.gxSwaped}</span></p>

              <span style={{float:'right'}}>
                  <Popover content={content3} trigger="click">
                        <QuestionCircleOutlined /> 
                  </Popover>&nbsp;&nbsp;
                  <Button onClick={() => {this.swapGX()}} 
                        disabled={this.state.disabled}
                        type="primary"  
                        size='small'>
                        运回
                    </Button>
              </span>
              
          </div>
          ,
    };
  return (
    <Card
      style={{ width: '100%',padding:"0px" }}
      tabList={tabListNoTitle}
      activeTabKey={this.state.noTitleKey}
      onTabChange={key => {
        this.onTabChange(key, 'noTitleKey');
      }}
    >
      {contentListNoTitle[this.state.noTitleKey]}
    </Card>
  );
}
}



export default maincard1;
