import React,{ Component }  from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Button} from 'antd';
import HangQin from '../../ABI/hangqin';
import erc20abi from '../../ABI/ERC20.abi.json';

class Hangqin extends Component {
      state = {
        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'',
        lastHBsymbol:'',
        message:"",
        
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']
        
        if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  const feeID = await HangQin.methods.feeID().call();

    const dataSource = [];
    for(var i=11; i<=feeID; i++ ){
      const tokenPrice = await HangQin.methods.getTOKENprice(i).call();
      var wei = '';
      if(tokenPrice[2].length > 22){wei = 0}else(wei= 3)
      tokenPrice[2] = parseFloat(web3.utils.fromWei(tokenPrice[2],"ether")).toFixed(wei);
      if(tokenPrice[0] != 0 ){
      dataSource.push(tokenPrice);
      }
    }
    for(var i=0; i<dataSource.length; i++ ){
    const ERC20adr = await HangQin.methods.getTOkenInfo(dataSource[i][0]).call();
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,ERC20adr[2]);
    const balance =  await ERC20.methods.balanceOf(window.defaultAccount).call();
    const __balance = parseFloat(balance*10**(- ERC20adr[5]) )
    const _balance = parseFloat(balance*10**(- ERC20adr[5]) ).toFixed(1)
    const balanceRMB = parseFloat(__balance * dataSource[i][2] ).toFixed(1)
    if(__balance != 0 ){dataSource[i][3]= _balance}
    if(balanceRMB != 0){dataSource[i][4]= balanceRMB}
    }


      this.setState({requests:dataSource});


  }


  render(){

    const columns = [
      {
        title: '名称',
        dataIndex: '1',
        width: 30,
        align: 'left',
      },
      {
        title: '数量',
        dataIndex: '3',
        width: 30,
        align: 'left',
      },
      
      
      {
        title: '折合￥',
        dataIndex: '4',
        width: 75,
        align: 'left',
      },

      {
        title: '价格(￥)',
        dataIndex: '2',
        align: 'right',
      }
    ];

  return (
    <div>
    <Card  type="inner" size='small' >

      <Table pagination={false } columns={columns}  dataSource={this.state.requests}  /> 
      
    </Card>
    <Card>
        <Button shape='round' type="primary" block color='green' size="middle" ><Link to="/Hangqinadd">添加我喜欢的通证</Link></Button>
        <div style={{textAlign:'center'}}>添加后全网可见！</div>
    </Card>

    </div>
  );
}
}

export default Hangqin;
